<template>
  <div id="customized-index">
    <search-header :hasBack="hasBack">
      <!-- <router-link :to="{ name : 'Index'}" slot="icon">
            <div class="message" @click="toMessage">
              <img src="../../assets/common_btn_message@2x.png"/>
              <span class="point">0</span>
            </div>
          </router-link> -->
    </search-header>


    <div class="category-list">
      <div class="list-item clearfix" v-for="(item,index) in categorysList" :key="index">
        <h3>{{item.name}}</h3>
        <div class="item-detail" v-for="(item) in item.categorys" :key="item.id" @click="toCategoryStoreList(item)">
          <img :src="item.imagePath">
        </div>

        </div>
      </div>
      <!-- <div class="list-item">
        <h3>定制用途</h3>
        <div class="item-detail">
          <img src="../../assets/pic.png">
        </div>
        <div class="item-detail">
          <img src="../../assets/pic.png">
        </div>
        <div class="item-detail">
          <img src="../../assets/pic.png">
        </div>
        <div class="item-detail">
          <img src="../../assets/pic.png">
        </div>
      </div> -->
    </div>

  
</template>

<script>
import SearchHeader from "@/components/Header/SearchHeader";

export default {
  data() {
    return {
      hasSeted: true,
      hasBack: true,
      customized: true,
      categorysList: []
    };
  },
  watch: {},
  created() {
    this.getCategoryList();
  },
  methods: {
    // UserDetails() {
    //   this.$Api.UserDetails(0).then(res => {
    //     console.log(res);
    //   });
    // },
    //获取分类列表
    getCategoryList() {
      this.$Api.getCategoryList(2).then(res => {
        console.log(res.q.categorys);
        this.categorysList = res.q.categorys;
      });
    },
    toCategoryStoreList(item) {
      this.$router.push({
        name: "CustomizedStoreList",
        query: { id: item.id }
      });
    }
  },
  components: {
    SearchHeader
  }
};
</script>

<style lang="scss">
@import "../../sass/baseSass.scss";
#customized-index {
  background: #f0f0f0;

  .list-item {
    background: #fff;
    width: box(750);
    margin-bottom: box(20);
    padding: 0 box(10) box(30);

    h3 {
      font-size: box(32);
      color: #323232;
      font-weight: 800;
      padding: box(30);
    }
    .item-detail {
      width: box(360);
      height: box(200);
      // background: #fc4518;
      float: left;
      position: relative;
      overflow: hidden;
      margin-bottom: box(10);

      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .item-detail:nth-child(even) {
      margin-right: box(10);
    }
    .mr10 {
      margin-right: box(10);
    }
    .clear {
      clear: both;
    }
  }
}
</style>



